import 'prestakit/dist/js/prestashop-ui-kit';

import {BridgeConfiguration} from './admin/src/classes/configuration';
import {BridgeFAQ} from './admin/src/classes/faq';

const bridgeConfig = new BridgeConfiguration();
bridgeConfig.init(bridgeConfig);

const bridgefaq = new BridgeFAQ();

var bridgeEvents = false;

document.onreadystatechange = function() {

    if (bridgeEvents === true) {
        return false;
    }
    $('#copy-clip-bridge').click(bridgeConfig.copyToClipboard);
    $('.bridge-collapse').click(bridgefaq.toggleAccordion);
    $('#prod_switch').click(bridgeConfig.hideZone);
    bridgeEvents = true;
};