export class BridgeConfiguration {

    init(className) {
        self.className = className;
    }

    copyToClipboard() {
        var text = $('#copy-clip-bridge').attr('data-clipboard-copy');
        try {
            self.className.jQueryCopy(text);
            self.className.showConfZone();
            return true;
        } catch (errorjQuery) {
            console.log('Error copy jQuery' + errorjQuery);
        }
        try {
            navigator.clipboard.writeText(text);
            this.showConfZone();
            return true;
        } catch(error) {
            console.log('Error writeText' + error);
        }
        try {
            navigator.clipboard.write(text);
            BridgeConfiguration.call(this.showConfZone);
            return true;
        } catch(errorWrite) {
            console.log('Error write' + errorWrite);
        }
    }

    hideZone() {
        var valTest = $('#production_mode_on').not(':checked').length > 0;
        var zoneToShow = 'data-test-zone';
        var zoneToHide = 'data-prod-zone';
        if (valTest === false) {
            zoneToShow = 'data-prod-zone';
            zoneToHide = 'data-test-zone';
        }
        $('div [' + zoneToShow + ']').removeClass('hidden');
        $('div [' + zoneToHide + ']').addClass('hidden');
    }    

    showConfZone() {
        $.growl({ message: bridge.translations.copy_link_webhook });
    }

    jQueryCopy(text) {
        var copyTextAreaBridge = document.createElement("textarea");
        document.body.appendChild(copyTextAreaBridge);
        copyTextAreaBridge.value = text;
        copyTextAreaBridge.select();
        document.execCommand("copy");
        document.body.removeChild(copyTextAreaBridge);
    }
}